.circularPattern {
  height: calc(100vh - 70px);
  width: 100vw; 
  --s: 128px;
  
  --_g: radial-gradient(#0000 70%,#1a2030 71%);
  background:
    var(--_g), var(--_g) calc(var(--s)/2) calc(var(--s)/2),
    conic-gradient(#0f9177 25%,#fdebad 0 50%,#d34434 0 75%,#b5d999 0);
  background-size: var(--s) var(--s); 
}